/*utils scss files */ /*========================
    Animation CSS start
==========================*/
@keyframes fireworkLine {
  0% {
    right: 20%;
    transform: scale(0, 0);
  }
  25% {
    right: 20%;
    width: 6px;
    transform: scale(1, 1);
  }
  35% {
    right: 0;
    width: 35%;
  }
  70% {
    right: 0;
    width: 4px;
    transform: scale(1, 1);
  }
  100% {
    right: 0;
    transform: scale(0, 0);
  }
}
@keyframes fireworkPoint {
  30% {
    transform: scale(0, 0);
  }
  60% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(0, 0);
  }
}
@keyframes rotating-design {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*========================
    Variable CSS start
==========================*/
/* font family */
/* color variables */
:root {
  --theme-color: 18, 38, 54;
  --white: 255, 255, 255;
  --black: 0, 0, 0;
  --light-text: 155, 163, 170;
  --error-color: 240, 73, 73;
  --success-color: 25, 135, 84;
  --box-bg: 32, 51, 66;
  --light-bg: 246, 246, 247;
  --secondary-color: 255, 193, 7;
}

.dark {
  --white: 7, 20, 31;
  --black: 255, 255, 255;
  --theme-color: 255, 255, 255;
  --light-text: 155, 163, 170;
  --box-bg: 60, 60, 60;
  --light-bg: 18, 38, 54;
  --secondary-color: 255, 193, 7;
  --error-color: 240, 73, 73;
  --success-color: 25, 135, 84;
}

/* base scss files */
/*=====================
    Reset CSS start
==========================*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.custom-container {
  padding: 0 20px;
}

section,
.section-t-space {
  padding-top: 25px;
}

.section-b-space {
  padding-bottom: 25px;
}

.section-sm-t-space {
  padding-top: 15px;
}

.section-sm-b-space {
  padding-bottom: 15px;
}

.panel-space {
  padding-top: 80px !important;
}

.theme-color {
  color: rgba(var(--theme-color), 1);
}

.light-text {
  color: rgba(var(--light-text), 1);
}

.error-color {
  color: rgba(var(--error-color), 1);
}

[dir=rtl] .swiper {
  direction: ltr;
}

/*=====================
    Typography CSS start
==========================*/
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  height: 100vh;
  background-color: rgba(var(--white), 1);
}
body::-webkit-scrollbar {
  width: 0;
}

h1 {
  font-weight: 600;
  font-size: 20px;
  line-height: 29px;
  margin-bottom: 0;
}

h2 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 0;
}

h3 {
  font-size: 15px;
  font-weight: 400;
  margin-bottom: 0;
}

h4 {
  font-size: 14px;
  margin-bottom: 0;
}

h5 {
  font-size: 13px;
  line-height: 1.2;
  margin-bottom: 0;
}

h6 {
  font-size: 12px;
  line-height: 1.2;
  margin-bottom: 0;
}

p {
  font-size: 13px;
  line-height: 1.5;
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin-bottom: 0;
}

li {
  list-style: none;
  display: inline-block;
  font-size: 14px;
}

/* components scss files*/
/*=====================
