/*=====================
    Button CSS start
==========================*/
.btn {
  margin-top: 30px;
  padding: calc(8px + 4 * (100vw - 320px) / 1600);
}
.btn-inline {
  padding-inline: calc(8px + 7 * (100vw - 320px) / 1600);
}
.btn-lg {
  padding: 12px 44px;
}
.btn:active {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
  border: 1px solid rgba(var(--theme-color), 1);
}

.theme-btn {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
  font-weight: 400;
  font-size: calc(16px + 2 * (100vw - 320px) / 1600);
  border-radius: 8px;
}
.theme-btn:hover {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
}
.theme-btn:active {
  background-color: rgba(var(--theme-color), 1);
  color: rgba(var(--white), 1);
  border: 1px solid rgba(var(--theme-color), 1);
}

.auth-btn {
  background-color: rgba(var(--secondary-color), 1);
  color: rgb(18, 38, 54);
  font-weight: 500;
  font-size: calc(16px + 2 * (100vw - 320px) / 1600);
  border-radius: 6px;
}
.auth-btn:hover {
  background-color: rgba(var(--secondary-color), 1);
  color: rgba(var(--theme-color), 1);
}

.gray-btn {
  background-color: rgba(var(--light-bg), 1);
  color: rgba(var(--light-text), 1);
  border-radius: 6px;
  font-weight: 400;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.gray-btn:active {
  border: none;
}
.gray-btn:hover {
  background-color: rgba(var(--light-bg), 1);
  color: rgba(var(--light-text), 1);
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: rgba(var(--light-text), 1);
}
.btn-link:hover {
  color: rgba(var(--light-text), 1);
}

.filter-btn {
  --Iconsax-Color: rgba(var(--theme-color), 1);
