  justify-content: center;
  align-items: center;
  padding: 8px;
  border: none;
}
.filter-btn:hover {
  background-color: rgba(var(--light-bg), 1);
}
.filter-btn:active {
  border: none;
  background-color: rgba(var(--light-bg), 1);
}
.filter-btn .filter-icon {
