.effect-group {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
[dir=rtl] .effect-group {
  left: unset;
  right: 0;
}
.effect-group .effect {
  display: block;
  position: absolute;
  top: 38%;
  left: 50%;
  width: 20px;
  transform-origin: 0px 2px;
}
.effect-group .effect:nth-child(2) {
  transform: rotate(72deg);
}
.effect-group .effect:nth-child(3) {
  transform: rotate(144deg);
}
.effect-group .effect:nth-child(4) {
  transform: rotate(216deg);
}
.effect-group .effect:nth-child(5) {
  transform: rotate(288deg);
}
.effect-group .effect:before {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  border-radius: 2px;
  height: 3px;
  background: #777777;
}
[dir=rtl] .effect-group .effect:before {
  right: unset;
  left: 0;
}
.effect-group .effect:after {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  right: 10%;
  border-radius: 50%;
  width: 3px;
  height: 3px;
  background: rgba(var(--theme-color), 1);
  transform: scale(0, 0);
}
[dir=rtl] .effect-group .effect:after {
  right: unset;
  left: 10%;
}

